.photo {
  position: absolute;


  & .right_top {
    padding: 38px 41px;
    position: fixed;
    width: 30%;
    height: 50%;
    margin-bottom: 30px;
    z-index: 5;


    & .border1 {
      position: absolute;
      z-index: 1;
      left: 0;
      bottom: 20px;
      right: 0;
      top: 20px;
      border: 1px solid rgba(255, 255, 255, .2);
      border-radius: 2px;
      transition: all .3s ease;
    }

    & .border2 {
      position: absolute;
      z-index: 1;
      left: 20px;
      bottom: 0;
      right: 20px;
      top: 0;
      border: 1px solid rgba(255, 255, 255, .2);
      border-radius: 2px;
      transition: all .3s ease;
    }

    & .img_holder {
      position: relative;
      z-index: 2;

      & .abs_img {
        position: absolute;
        z-index: 1;
        left: 0;
        bottom: 0;
        right: 0;
        top: 0;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
      }

      img {
        min-width: 100%;
        opacity: 0;
      }
    }
  }

  & .title_holder {
    text-align: center;
    padding: 27px 0 20px;
  }
}

